%center {
  display: flex;
  justify-content: center;
}

body {
	height: 100vh;
}

#particles-js {
	position: fixed;
	z-index: -1;
	width: 100vw;
	height: 100vh;
}


.main-text {
	width: 100%;
	margin-top: 40px;
	h2 {
		text-align: center;
		padding-bottom: 40px;
		border-bottom: 2px solid red;
		font-size: 36px;
	}
}

.cources {

display: grid;

grid-template-columns: 1fr 1fr 1fr 1fr 1fr;

grid-template-rows: 1fr 20px 1fr 20px 1fr;

grid-column-gap: 0px;

grid-row-gap: 0px;


.item-1 { grid-area: 1 / 2 / 2 / 3; }

.item-2 { grid-area: 1 / 3 / 2 / 4; }

.item-3 { grid-area: 1 / 4 / 2 / 5; }

.item-4 { grid-area: 3 / 2 / 4 / 3; }

.item-5 { grid-area: 3 / 3 / 4 / 4; }

.item-6 { grid-area: 3 / 4 / 4 / 5; }

.item-7 { grid-area: 5 / 2 / 6 / 3; }

.item-8 { grid-area: 5 / 3 / 6 / 4; }

.item-9 { grid-area: 5 / 4 / 6 / 5; }

}

.cource {
	width: 300px;
	height: 400px;
	margin-top: 40px;
	border: 1px solid #111;
	@extend %center;
	align-items: center;
	background: #212121;
	transition: all .5s ease-in-out;
    .content {
    	width: 100%;
		h3 {
			text-align: center;
			font-size: 22px;
			color: #FF3115;
			margin-bottom: 30px;
		}
		p {
			text-align: center;
			color: #fff;
		}
		.button {
			@extend %center;
			align-items: center;
			background: #FF3115;
			height: 40px;
			margin-top: 40px;
			a {
				text-decoration: none;
				color: #fff;
			}
		}
    }
}

.cource:hover {
	background: #34ABE3;
	color: #000;
}
