* {
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Roboto', sans-serif;
}
$color-main2: #FF3115;
$color-main1: #888888;
%space-between {
  display: flex;
  justify-content: space-between;
}
%center {
  display: flex;
  justify-content: center;
}
.center {
  width: 100%;
  @extend %center;
}
i {
  color: $color-main2;
}

.width60 {
  width: 60%;
  @extend %space-between;
}

.dark-line {
  background: black;
  width: 100%;
  height: 35px;
  @extend %center;
  @extend %space-between;
      .welcome {
        font-size: 13px;
        color: white;
        margin-top: 10px;
         p {
          display: inline;
         }
      }
      .networks {
        color: $color-main1;
        margin: 10px 0 0 0;
      }
    }

.white-line {
  width: 100%;
  @extend %center;
   .logo {
    margin: 20px 0 0 0;
    i {
      font-size: 40px;
    }
    p {
      font-size: 40px;
      font-weight: 1000;
      display: inline;
    }
   }
   .contact {
    @extend %space-between;
    width: 660px;
     .telegram i{
      color: #34ABE3 !important;
     }
     .Us {
      margin: 20px 0 0 0;
      span {
        font-weight: 1000;
        font-size: 19px;
       }
      p {
        position: relative;
        left: 38px;
      }
       i {
        font-size: 35px;
        display: inline;
       }
     }
   }
}

.menu {
  @extend %center;
  height: 60px;
  margin-top: 20px;
  position: relative;
  z-index: 1;
   .menu-container {
    width: 60%;
    background: #111111;
    display: flex;
    justify-content: center;
    align-items: center;
      a {
       padding-right: 40px;
       text-decoration: none;
       color: #989898;
       font-size: 22px;
       span {
        color: $color-main2;
       }
      }
      a:hover {
        color: $color-main2;
      }    
  }
}

